import { Redirect } from "react-router";
import { useEffect, useState } from "react";
import axios from "axios";

let validateEmail = (email) => {
  const re =
    /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
};

let UserScore = ({ user, testScore, hiringManagerEmail }) => {
  let [submitted, setSubmitted] = useState(false);
  useEffect(() => {
    let totalScore = 0;
    let formattedScoreForApi = [];
    for (let x in testScore) {
      totalScore += testScore[x];
    }
    for (let i = 1; i <= 5; i++) {
      formattedScoreForApi[i - 1] = testScore[`level${i}`];
    }

    let dataToBeSent = {
      email: user.email,
      individualScore: formattedScoreForApi,
      totalScore,
    };

    if (hiringManagerEmail && validateEmail(hiringManagerEmail)) {
      dataToBeSent.hiringManagerEmail = hiringManagerEmail;
    }

    console.log(dataToBeSent);

    axios
      .post(
        "https://livioscreeningapi.herokuapp.com/screening/submit",
        dataToBeSent
      )
      .then(() => {
        setSubmitted(true);
      })
      .catch((error) => {
        alert("Error!");
        console.log(error);
      });
  }, []);
  return (
    <>
      {user ? (
        <div className="container p-4">
          <h5>{user.name}</h5>
          <h5>{user.email}</h5>
          <hr />
          {submitted ? (
            <h2>
              Thank you for applying to Livio. Your Test has been submitted
            </h2>
          ) : (
            <h2>Please wait while we submit your test.</h2>
          )}
        </div>
      ) : (
        <Redirect to="/" />
      )}
    </>
  );
};

export default UserScore;
